.weather-container{
    position: relative;
    display:flex;
    top:320px;
    justify-content: center;
    right:-620px;
    
    width:fit-content;
    margin-right:-100px;


    border-radius: 15px;
    box-shadow:inset 0 8px 22px 5px #8b48d7;
    text-align:center;
    backdrop-filter: blur(20px);
    
}

input{
    width: calc(100%-16px);
    padding: 1px;
    height:35px;
    box-sizing: border-box;
    border-radius:10px;
    border: 1px solid white;
    margin-top:20px;
    outline:none;
    position:relative;
    top:-19px
}
input:hover{
    border: 1.5px solid #e1a50d;
}
button{
    background:#8424c8;
    color:white;
    padding: 3px;
    border:none;
    border-radius:  10px;
    cursor: pointer;

    width: 100px;
    height: 35px;
    position: relative;
    top:0.2px;
    font-size: 15px;
    left:10px;

}
button:hover{
    background: #b987f2;
}
.weather-info{
    font-size: 8em;
    color: #d7d7d7;
    position:relative;
    left: 70px;
}
h2,p{
    color: var(--primary-text-color);
    margin: 8px;
}
.humidty-wind{
    display:flex;
    flex-direction: row;
    justify-content: space-around;

}

